:root {
  --main-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header img {
  display: flex;
}

.nav {
  margin: 0;
  padding: 0;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .2s;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  transition: all .2s;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
